import React, { Component } from 'react'
import '@hi-ui/hiui/es/table/style/index.css'
import { Form, Input, Button, DatePicker, Counter, Select, Radio, Modal, Grid } from '@hi-ui/hiui'
import './index.scss'

const FormItem = Form.Item

export default class Template extends Component {
  constructor (props) {
    super(props)
    this.state = {
      forms: this.initForms(),
      smallShow: false,
      bigShow: false,
      rules: {
        text: [
          {
            required: true,
            message: <span style={{ color: '#ccc' }}></span>,
            trigger: 'onBlur,onChange'
          }
        ],
        date: [
          {
            required: true,
            message: <span style={{ color: '#ccc' }}></span>,
            trigger: 'onBlur,onChange'
          }
        ]
      }
    }
    this.singleList = [
      { title: '', id: '2' },
      { title: '', id: '3' },
      { title: '', id: '4', disabled: true },
      { title: '', id: '5' },
      { title: '', id: '6' }
    ]
  }

  initForms () {
    return Object.assign(
      {},
      {
        text: '',
        Date: { start: new Date(), end: new Date() },
        num: 0,
        time: new Date(),
        select: '4',
        radio: '',
        longText: ''
      }
    )
  }

  handleChange () {}

  handleSubmit () {}

  reset () {}

  triggerBig (value) {
    this.setState({ bigShow: value })
  }

  triggerSmall (value) {
    this.setState({ smallShow: value })
  }

  render () {
    const Row = Grid.Row
    const Col = Grid.Col
    const { forms } = this.state

    return (
      <div className='page--form-modal'>
        <h2 className='hi-form__title'></h2>

        <Button type='primary' onClick={this.triggerBig.bind(this, true)}>
          
        </Button>
        <Button type='primary' onClick={this.triggerSmall.bind(this, true)}>
          
        </Button>

        <Modal
          title=''
          visible={this.state.bigShow}
          onConfirm={this.triggerBig.bind(this, false)}
          onCancel={this.triggerBig.bind(this, false)}
          width={800}
          footer={[
            <Button type='primary' key={1} onClick={this.handleSubmit.bind(this)}>
              
            </Button>,
            <Button type='line' key={2} onClick={this.reset.bind(this)}>
              
            </Button>
          ]}
        >
          <Form
            ref={this.form1}
            model={forms}
            rules={this.state.rules}
            labelWidth='90'
            labelPlacement='top'
          >
            <Row>
              <Col span={12}>
                <FormItem label='' field='text'>
                  <Input
                    value={forms.text}
                    placeholder={'name'}
                    onChange={this.handleChange.bind(this, 'column1')}
                    style={{ width: '320px' }}
                  />
                </FormItem>
                <FormItem label='' field='date'>
                  <DatePicker
                    type='daterange'
                    value={forms.Date}
                    onChange={d => {
                      console.log(d)
                    }}
                  />
                </FormItem>
                <FormItem label='' field='longText'>
                  <Input
                    value={forms.longText}
                    placeholder={''}
                    onChange={this.handleChange.bind(this, 'column1')}
                    style={{ width: '320px', height: '100px', resize: 'none' }}
                    type='textarea'
                  />
                </FormItem>
              </Col>
              <Col span={12}>
                <FormItem label='' field='select'>
                  <Select
                    data={this.singleList}
                    placeholder=''
                    style={{ width: '200px' }}
                    value={forms.select}
                    onChange={item => {
                      console.log('', item)
                    }}
                  />
                </FormItem>
                <FormItem label='' field='num'>
                  <Counter
                    value={forms.num}
                    step={1}
                    min={0}
                    max={8}
                    onChange={val => console.log('', val)}
                  />
                </FormItem>
                <FormItem label='' field='radio'>
                  <Radio.Group
                    data={['', '', '', '', '', '']}
                    checked={forms.radio}
                    onChange={this.handleChange.bind(this, 'region', '')}
                  />
                </FormItem>
              </Col>
            </Row>
          </Form>
        </Modal>

        <Modal
          title=''
          visible={this.state.smallShow}
          onConfirm={this.triggerSmall.bind(this, false)}
          onCancel={this.triggerSmall.bind(this, false)}
          width={600}
          footer={[
            <Button type='primary' key={1} onClick={this.handleSubmit.bind(this)}>
              
            </Button>,
            <Button type='line' key={2} onClick={this.reset.bind(this)}>
              
            </Button>
          ]}
        >
          <Form
            ref={this.form1}
            model={forms}
            rules={this.state.rules}
            labelWidth='120'
            labelPlacement='right'
          >
            <FormItem label='' field='text'>
              <Input
                value={forms.text}
                placeholder={'name'}
                onChange={this.handleChange.bind(this, 'column1')}
                style={{ width: '250px' }}
              />
            </FormItem>
            <FormItem label='' field='Date'>
              <DatePicker
                type='daterange'
                value={forms.Date}
                onChange={d => {
                  console.log(d)
                }}
              />
            </FormItem>
            <FormItem label='' field='num'>
              <Counter
                defaultValue={forms.num}
                step={1}
                min={0}
                max={8}
                onChange={(e, val) => console.log('', val)}
              />
            </FormItem>
            <FormItem label='' field='select'>
              <Select
                data={this.singleList}
                placeholder=''
                style={{ width: '200px' }}
                value={forms.select}
                onChange={item => {
                  console.log('', item)
                }}
              />
            </FormItem>
          </Form>
        </Modal>
      </div>
    )
  }
}
