import React, { Component } from 'react'
import '@hi-ui/hiui/es/table/style/index.css'
import {
  Form,
  Input,
  Button,
  DatePicker,
  Counter,
  TimePicker,
  Select,
  Radio,
  Upload,
  Grid
} from '@hi-ui/hiui'
import './index.scss'

const FormItem = Form.Item

export default class Template extends Component {
  constructor (props) {
    super(props)
    this.state = {
      forms: this.initForms(),
      rules: {
        text: [
          {
            required: true,
            message: <span style={{ color: '#ccc' }}></span>,
            trigger: 'onBlur,onChange'
          }
        ],
        date: [
          {
            required: true,
            message: <span style={{ color: '#ccc' }}></span>,
            trigger: 'onBlur,onChange'
          }
        ]
      }
    }
    this.singleList = [
      { title: '', id: '2' },
      { title: '', id: '3' },
      { title: '', id: '4', disabled: true },
      { title: '', id: '5' },
      { title: '', id: '6' }
    ]
  }

  initForms () {
    return Object.assign(
      {},
      {
        text: '',
        Date: { start: new Date(), end: new Date() },
        num: 0,
        time: new Date(),
        select: 1,
        radio: '',
        longText: ''
      }
    )
  }

  handleChange () {}

  handleSubmit () {}

  reset () {}

  render () {
    const Row = Grid.Row
    const Col = Grid.Col
    const { forms } = this.state

    return (
      <div className='page--form-group'>
        <Form ref={this.form} model={forms} rules={this.state.rules} labelWidth='120'>
          <Row>
            <Col span={24}>
              <h2 className='hi-form__title'></h2>
              <fieldset>
                <legend></legend>

                <FormItem label='' field='text'>
                  <Input
                    value={forms.text}
                    placeholder={''}
                    onChange={this.handleChange.bind(this, 'column1')}
                    style={{ width: '250px' }}
                  />
                </FormItem>
                <FormItem label='' field='date'>
                  <DatePicker
                    type='daterange'
                    value={forms.date}
                    onChange={d => {
                      console.log(d)
                    }}
                  />
                </FormItem>
                <FormItem label='' field='num'>
                  <Counter
                    defaultValue={forms.num}
                    step={1}
                    min={0}
                    max={8}
                    onChange={(e, val) => console.log('', val)}
                  />
                </FormItem>
              </fieldset>
              <fieldset>
                <legend></legend>

                <FormItem label='' field='time'>
                  <TimePicker
                    value={forms.time}
                    onChange={d => {
                      console.log(d)
                    }}
                  />
                </FormItem>
                <FormItem label='' field='select'>
                  <Select
                    data={this.singleList}
                    placeholder=''
                    style={{ width: '200px' }}
                    value={forms.select}
                    onChange={item => {
                      console.log('', item)
                    }}
                  />
                </FormItem>
              </fieldset>
              <fieldset>
                <legend></legend>

                <FormItem label='' field='radio'>
                  <Radio.Group
                    data={['', '', '']}
                    defaultValue={forms.radio}
                    onChange={this.handleChange.bind(this, 'region', '')}
                  />
                </FormItem>

                <FormItem label='' field='radio'>
                  <Upload
                    type='photo'
                    uploadAction='http://127.0.0.1:8000'
                    params={{ id: 'uid', channel: 'youpin' }}
                    name={'files[]'}
                  />
                </FormItem>

                <FormItem label='' field='longText'>
                  <Input
                    value={forms.longText}
                    placeholder={''}
                    onChange={this.handleChange.bind(this, 'column1')}
                    style={{ width: '320px', height: '100px' }}
                    type='textarea'
                  />
                </FormItem>
              </fieldset>
              <fieldset>
                <FormItem>
                  <Button type='primary' onClick={this.handleSubmit.bind(this)}>
                    
                  </Button>
                  <Button
                    type='line'
                    onClick={this.reset.bind(this)}
                    style={{ marginLeft: '16px' }}
                  >
                    
                  </Button>
                </FormItem>
              </fieldset>
            </Col>
          </Row>
        </Form>
      </div>
    )
  }
}
