const transformConfig = (config) => {
  config.forEach((c) => {
    if (c.path) {
      c.component = components[c.path.split('/')[1]]
    }
    if (c.children) {
      transformConfig(c.children)
    }
  })
  return config
}

export default transformConfig(config)
